
.graph-legend-item {
  display: inline-block;
  width: 50%;
  font-family: futura-pt, sans-serif;
  font-weight: 400;
  font-size: .8em;
  line-height: .8em;
  letter-spacing: .01em;
  margin-bottom: 10px;
  cursor: pointer;
}
.graph-legend-item.opacity {
  opacity: 0.2;
  /*transition: opacity 250ms ease;*/
}
.graph-legend-item.opacity:hover {
  opacity: 0.75;
}
.graph-legend-item a {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.item-legend-color {
  width: 10px;
  height: 10px;
  display: inline-block;
  margin-right: 10px;
  transform-origin: 50% 50%;
  transition: transform 200ms ease;
}
.item-percent {
  font-family: futura-pt, sans-serif;
  font-weight: 600;
  font-size: 1.2em;
  margin-left: 7px;
  display: none;
}
.graph-legend-item:hover .item-percent{
  display: inline-block;
}
.graph-legend-item:hover .item-legend-color{
  transform: rotate(45deg);
}




