
.graph-legend-items {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin-top: 20px;
  max-height: 285px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.legend-title {
  font-family: futura-pt, sans-serif;
  font-weight: 700;
  font-size: 1.1em;
  line-height: 1.1em;
  text-transform: uppercase;
}
.legend-title::after {
  content: '';
  display: block;
  width: 40px;
  height: 1px;
  padding-top: 15px;
  border-bottom: 1px solid #000;
}
.slide-top-enter-active {
  transition: margin .3s ease, opacity 250ms ease-out;
}
.slide-top-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-top-enter, .slide-top-leave-active {
  margin-top: -10px;
  opacity: 0;
}




